<template>
  <div class="login">
    <div class="container">
      <div class="name-password">
        <input
          type="password"
          v-model="passWord"
          class="common"
          placeholder="请输入密码"
          ref="input"
        />
        <button @click="login()" class="common">登录</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        passWord: '',
      }
    },
    created() {
      if (localStorage.getItem('passWord') === 'wa') {
        this.$router.push('/')
      }
    },
    methods: {
      login() {
        localStorage.setItem('passWord', this.passWord)
        if (this.passWord === 'wa') {
          this.$router.push('/')
        }
      },
    },
    mounted() {
      this.$refs.input.focus()
    },
  }
</script>
